import React, { useEffect, useCallback, useState} from 'react';
import {useFlags} from 'flags/flags.js';
import {CHAOS_COLLECTION} from 'constants/collections.js';
import DimensionStats from './DimensionStats.js';
import {connect} from 'react-redux';
import {remixesBy} from 'stats/stats.js';
import './analytics.scss';
import {useTokenMetadata} from 'components/hooks/useTokenMetadata.js';
import {getMetadata} from 'jacker/jack.js';

const Analytics = (props) => { 

    useFlags();

    let [collectionAddress, setCollectionAddress] = useState();
    let [stats, setStats] = useState({});
    let [mostPopularSongs, setMostPopularSongs] = useState({});
    let [topFive, setTopFive] = useState([]);
    let [dim, setDim] = useState("creator");
    let getTokenMetadata = useTokenMetadata();

    useEffect(() => {
        remixesBy(collectionAddress, dim).then(setStats);
        remixesBy(collectionAddress, "collectionAddress", "tokenId").then((s) => {
            let keys = Object.keys(s);
            keys.sort((a,b) => s[b].length - s[a].length);
            let most = keys.map(
                x => ({
                    collectionAddress: x.split(".")[0],
                    tokenId: x.split(".")[1]
                }));
            setMostPopularSongs(most);
            });
    }, [collectionAddress, dim]);

    useEffect(() => {
        if (mostPopularSongs.length > 0) {
            let most = mostPopularSongs[0];
            getTopFive(mostPopularSongs, getTokenMetadata).then(setTopFive);
       }
    }, [mostPopularSongs, getTokenMetadata]);

    let dimensionValues = Object.keys(stats);

    return (
        <div className="analytics">
          <div className="analytics-numbers">
            <div className="analytics-number">
              {dimensionValues.length} unique wallets
            </div>
            <div className="analytics-number">
              {Object.values(stats).flat(16).length} total remixes
            </div>
            <div className="analytics-number top-five">
              <div className="top-five-header">Most Remixed</div>
              {topFive.map((x, i) => <div className="top-song">{i+1}. {x.name}</div>)}
            </div>
          </div>

          <div className="analytics-table">
            <div className="dimension-stats stats-header">
              <div className="d-element">Wallet</div>
              <div className="d-element">Total Remixes</div>
              <div className="d-element">Unique Songs Remixed</div>
            </div>
            <div className="dimension-results">
              {dimensionValues.map(
                  x => <DimensionStats dimensionValue={x} dimension={dim} stats={stats[x]}/>)}
            </div>
          </div>
        </div>);
};

const getTopFive = async (list, getTokenMetadata) => {
    let fetched = [];
    for (let i=0; i < 5; i++) {
        let uri = await getTokenMetadata(list[i].collectionAddress, list[i].tokenId);
        let meta = await getMetadata(uri);
        fetched.push(meta);
    }
    return fetched;
}
 

export default Analytics;
