import React, { useRef, useEffect, useCallback, useState} from 'react';
import {setSporeTokenId} from 'actions/index.js';
import {useConsumeWheel} from 'components/hooks/useConsumeWheel.js';
import {connect} from 'react-redux';
import {useTokensOnSale} from 'components/hooks/useTokensOnSale.js';
import './menu.scss';

const PlayerChooserMenu = ({tokenId, setSporeTokenId}) => { 
    let ref = useRef();
    let {tokensOnSale} = useTokensOnSale();
    let [open, setOpen] = useState(false);
    let players = new Array(20).fill(0);
    for (let i=1; i <= 20; i++) {
        players[i-1] = i;
    }
    useConsumeWheel(ref);

    const loadPlayer = useCallback((tokenId) => {
        window.stemIFrame.current.contentWindow.postMessage({
            type: "tokenId",
            body: tokenId
        }, "*");
        setSporeTokenId(tokenId);
    }, []);

    console.log('tokensOnSale = ', tokensOnSale);
    return (
        <div
          ref={ref}
          onClick={(e) => {
            e.stopPropagation();
            setOpen(!open);
          }} className={open ? "player-menu opened" : "player-menu"}>
          SP-{tokenId}
          <div onClick={() => setOpen(!open)} className={open ? "arrow-up" : "arrow-down"}/>
          
          {open &&
           <div className="player-menu-items">
           {players.map(
               _tokenId => <div
                             onClick={() => loadPlayer(_tokenId)}
                             className={tokenId === _tokenId ? "selected go-to-player" : "go-to-player"}>
                             <div>SP-{_tokenId}</div>
                             {tokensOnSale.find(x => x.tokenId === _tokenId && x.askPrice) && <img className="for-sale" src="/sale-cart-icon.svg"/>}
                             <img className="menu-thumb" src={"/sp-thumbnail-" + _tokenId + ".png"}/>
                           </div>)}
           </div>}
        </div>
    );
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
      setSporeTokenId: x => dispatch(setSporeTokenId(x))
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(PlayerChooserMenu);
