import React, { useEffect, useRef, useCallback, useState} from 'react';
import {newPlaylist} from 'spores/playlist.js';
import {useSigner} from 'wagmi';
import {usePlaylistStorage} from './usePlaylistStorage.js';
import PlaylistSongs from './PlaylistSongs.js';
import { ConnectButton, darkTheme } from '@rainbow-me/rainbowkit';
import TokenMetadata from 'components/metadata/TokenMetadata.js';
import {connect} from 'react-redux';
import ImageHandler from 'components/build-a-spore/ImageHandler.js';
import './playlist-creator.scss';
import {useJacker} from 'components/hooks/useJacker.js';

const PlaylistCreator = (props) => { 
    let [link, setLink] = useState('');
    let [name, setName] = useState('');
    let [description, setDescription] = useState('');
    let [playlist, setPlaylist] = useState([]);
    let [imageHash, setImageHash] = useState(undefined);
    const { data: signer, isError, isLoading } = useSigner();

    usePlaylistStorage({playlist, setPlaylist});

    let {
        clear,
        onJack,
        artist,
        collectionAddress,
        tokenId,
        songName,
        coverArt,
        metadata,
        setArtist
    } = useJacker({updateURL: false, link, setLink, setShowLinkInput: () => 0, setShowHistory: () => 0});

    const addSong = useCallback(() => {
        if (!collectionAddress) {
            return;
        }
        let data = {
            artist,
            collectionAddress,
            songName,
            tokenId,
            metadata,
            coverArt
        };
        setPlaylist([... playlist, data]);
        clear();
    }, [setPlaylist, playlist, artist, collectionAddress, songName, tokenId, metadata, coverArt]);

    useEffect(() => {
        if (link !== "") {
            onJack(link);
        }
    }, [link]);

    const save = useCallback(() => {
        let collectionAddresses = playlist.map(x => x.collectionAddress);
        let tokenIds = playlist.map(x => x.tokenId);
        newPlaylist(signer, collectionAddresses, tokenIds, name, description, imageHash, playlist);
    }, [playlist, name, description, imageHash, signer]);


    return (
        <div className="playlist-creator nft-jacker">
          <div className="save-panel">
            <div className="connect-button-wrapper">
              <ConnectButton
                accountStatus="address" 
                showBalance={{ smallScreen: false, largeScreen: false }}
                label="Connect Wallet"
              />
            </div>
            <div
              onClick={save}
              className="save-btn pushable">
              Save
            </div>
          </div>
          <div className="playlist-details">
            <div className="details-inputs">
              <ItemInput
                get={name}
                set={setName}
                name="Playlist Name"
              />
              <ItemInput
                get={description}
                set={setDescription}
                name="Description"
              />
            </div>
            <ImageHandler imageHash={imageHash} setImageHash={setImageHash}/>
            <PlaylistSongs playlist={playlist} setPlaylist={setPlaylist}/>
          </div>
          <div className="add-item">
            <ItemInput
              get={link}
              set={setLink}
              name="Link to NFT"
            />
            {coverArt && <TokenMetadata artist={undefined} coverArt={coverArt} songName={songName}>
                           <ItemInput get={artist} set={setArtist} name="Artist"/>
                         </TokenMetadata>}
            <div onClick={addSong} className="pushable add-btn">
              +
            </div>
          </div>
        </div>
    );
};

const ItemInput = ({get, set, name}) => {
    return (
        <div className="item-input">
          <div className="item-name">
            {name}
          </div>
          <input value={get} onChange={(e) => set(e.target.value)} className="item-input-field"/>
        </div>
    );
};

export default PlaylistCreator;
