import React, { useEffect, useCallback, useState} from 'react';
import DimensionAggregation from './DimensionAggregation.js';
import DimensionValue from './DimensionValue.js';
import {connect} from 'react-redux';
import {by} from 'stats/stats.js';

const DimensionStats = ({stats, dimensionValue, dimension}) => { 
    return (
        <div className="dimension-stats">
          <DimensionValue dimensionValue={dimensionValue} dimension={dimension}/>
          <DimensionAggregation stats={stats} dimension={dimension}/>
          <DimensionAggregation stats={Object.keys(by(stats, "collectionAddress", "tokenId"))} dimension={"collectionAddress"}/>
        </div>
    );
};

export default DimensionStats;
