import React, {useEffect} from 'react';

export const usePlaylistStorage = ({playlist, setPlaylist}) => {
    useEffect(() => {
        let play = window.localStorage.getItem("unsaved-playlist");
        if (play) {
            setPlaylist(JSON.parse(play));
        }
    }, []);
    useEffect(() => {
        if (playlist.length > 0) {
            window.localStorage.setItem("unsaved-playlist", JSON.stringify(playlist));
        }
    }, [playlist]);
};
