import React, { useEffect, useCallback, useState} from 'react';
import RemixPlayer from './RemixPlayer.js';
import RemixesByTokenId from './RemixesByTokenId.js';
import {connect} from 'react-redux';
import {getSporeRemixes} from 'spores/getSporeRemixes.js';
import {remixesBy} from 'stats/stats.js';
import {CHAOS_COLLECTION} from 'constants/collections.js';
import './remixes.scss';

const RemixesPage = (props) => { 
    let [remixToPlay, setRemixToPlay] = useState(undefined);
    let [remixes, setRemixes] = useState({});
    let [collection, setCollection] = useState(CHAOS_COLLECTION);
    useEffect(() => {
        remixesBy(collection, "tokenId").then((x) => {
            setRemixes(x);
        });
    }, [setRemixes, collection]);


    let tokenIds = Object.keys(remixes);
    return (
        <div className="remixes-page nft-jacker jacked">
          {remixToPlay &&
           <div className="player-container show">
             <RemixPlayer
               songName={remixToPlay.name}
               artist={"Chaos"}
               remix={remixToPlay}
               setRemix={setRemixToPlay}/></div>
          }
          {tokenIds.map(
            tokenId => <RemixesByTokenId 
                         loadRemix={setRemixToPlay}
                         tokenId={tokenId} 
                         collectionAddress={collection} 
                         remixes={remixes[tokenId]}/>)}
        </div>);
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(RemixesPage);
