import React, { useRef, useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import {useVideoShader} from './useVideoShader.js';

const VideoShader = ({videoRef, uniforms, fragmentShader, vertexShader, width, height}) => { 
    let ref = useRef();
    useVideoShader({ref, videoRef, fragmentShader, uniforms, vertexShader, width, height});
    return (
        <div ref={ref} className="video-backdrop-shader sm:bottom-0 h-full w-full absolute md:top-0 left-0 z-0">
        </div>);
};

export default VideoShader;
