import React, { useEffect, useCallback, useState} from 'react';
import TokenMetadata from 'components/metadata/TokenMetadata.js';
import {connect} from 'react-redux';

const PlaylistSongs = ({playlist, setPlaylist}) => { 
    let [selected, setSelected] = useState(undefined);
    const [dragging, setDragging] = useState(undefined);

    useEffect(() => {
        window.addEventListener("click", clearSelect);
        window.addEventListener("keydown", onKey);
        return () => {
            window.removeEventListener("click", clearSelect);
            window.removeEventListener("keydown", onKey);
        }
    }, [setSelected, setPlaylist, playlist, selected]);

    const onKey = useCallback(e => {
        if (e.key === "Backspace") {
            setPlaylist(playlist.filter(x => x !== selected));
        }
    }, [selected, setPlaylist, playlist]);

    const clearSelect = useCallback(() => setSelected(undefined), [setSelected]);
    
    const onDragStart = useCallback(song => {
        setDragging(song);
    }, [setDragging]);

    const onDrop = useCallback(position => {
        // put dragging before the current song

        let _playlist = playlist.filter(x => x !== dragging);
        _playlist.splice(position, 0, dragging);
        setPlaylist(_playlist);
        
    }, [dragging, playlist]);

    return (
        <div className="playlist-songs">
          {playlist.map((song, i) =>
              <div
                onClick={(e) => {
                    e.stopPropagation();
                    setSelected(song);
                }}
                onDrop={(x) => onDrop(i)}
                onDragOver={(e) => e.preventDefault()}
                onDragStart={(x) => onDragStart(song)}
                draggable={true}
                className={selected === song ? "song-container selected" : "song-container"}>
                <div className="song-number">
                  {i+1}
                </div>
                <TokenMetadata {...song} />
              </div>
          )}
        </div>);
};


export default PlaylistSongs;
