import React, { useEffect, useCallback, useState} from 'react';
import ENSName from 'components/ens/ENSName.js';
import {TWINS_COLLECTION} from 'constants/collections.js';
import {connect} from 'react-redux';
import RemixPlayer from './RemixPlayer.js';
import {getSporeRemixes} from 'spores/getSporeRemixes.js';
import './remix-viewer.scss';

const YingYangTwins = ({params, match}) => { 
    let [remixData, setRemixData] = useState(undefined);
    let [remixes, setRemixes] = useState([]);

    useEffect(() => {
        getSporeRemixes(TWINS_COLLECTION).then(setRemixes);
    }, [setRemixes]);

    return (
        <div className="nft-jacker jacked remix-viewer">
          <img className="ying-yang-cover" src="/ying-yang-cover.jpg"/>
          <div className="remixes-list">
            {remixes.map(
                (x, i) =>
                <div
                  onClick={() => setRemixData(x)}
                  className="remix-option pushable">
                  <div className="spore-id">
                    Remix #{remixes.length - i}
                  </div>
                  <ENSName address={x.creator}/>
                </div>)}
          </div>
          {remixData && <div className={"player-container show"}>
                          <RemixPlayer
                            setRemix={setRemixData}
                            artist="Vic Mensa x Keyon Christ"
                            songName="Ying Yang Twins"
                            shareLink={"https://spores.vision/remix/" + remixData.sporeHash + "/" + remixData.dataHash}
                            remix={remixData}/>
                        </div>}
        </div>);
};

export default YingYangTwins;
