import * as THREE from "three";
import { extend } from "@react-three/fiber";
import { fragmentShader } from "../video/regular-shader/fragment";
import { vertexShader } from "../video/blurred-shader/vertex";

export default class PlaneShader extends THREE.ShaderMaterial {
  constructor() {
    super({
      uniforms: {
        video: { type: "t", value: undefined},
        width: { type: "f", value: 0},
        height: { type: "f", value: 0},
      },
      vertexShader: vertexShader,
      fragmentShader: fragmentShader
    })
  }

    set videoUniforms(u) {
        this.uniforms = {
            ... this.uniforms,
            ... u.current
        };
    }
    set video(v) {
        this.uniforms.video.value = v
    }
    set width(v) {
        this.uniforms.width.value = v
    }
    set height(v) {
        this.uniforms.height.value = v
    }
}

// register element in r3f (<fade />)
extend({ PlaneShader });
