import React, { useEffect, useCallback, useState} from 'react';
import Slider from 'components/sliders/Slider.js';
import {connect} from 'react-redux';
import './parameters.scss';

const Parameters = ({playerRef, imageHash}) => { 
    let [smoke, setSmoke] = useState(0);
    let [swirl, setSwirl] = useState(0);

    useEffect(() => {
        if (imageHash) {
            setSmoke(0.1 + 0.4 * Math.random());
            setSwirl(0.2 + 0.6 * Math.random());
        }
    }, [imageHash, setSmoke, setSwirl]);

    useEffect(() => {
        playerRef.current.contentWindow.postMessage({
            type: "skin-smoke",
            body: smoke*.99 + 0.01
        }, "*");
    }, [smoke]);

    useEffect(() => {
        playerRef.current.contentWindow.postMessage({
            type: "skin-swirling",
            body: swirl*0.99 + 0.01
        }, "*");
    }, [swirl]);

    return (<div className="parameters">
              <div className="param">
                <div className="param-label">
                  Smoke
                </div>
                <Slider value={smoke} setValue={setSmoke}/>
              </div>
              <div className="param">
                <div className="param-label">
                  Swirl
                </div>
                <Slider value={swirl} setValue={setSwirl}/>
              </div>
            </div>);
};

export default Parameters;
