import React, { useRef, useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import { HexColorPicker } from "react-colorful";

const SliderColor = ({playerRef, extractedColor="#1f3fab"}) => { 
    let [color, setColor] = useState(extractedColor);
    let last = useRef(0);
    
    const onChange = useCallback((c) => {
        let now = new Date().getTime();
        if (now - last.current < 10) {
            return;
        }
        last.current = now;
        playerRef.current.contentWindow.postMessage({
            type: "slider-color",
            body: c 
        }, "*");
       setColor(c);
    }, [setColor]);

    useEffect(() => {
        if (extractedColor) {
            setColor(extractedColor);
            playerRef.current.contentWindow.postMessage({
                type: "slider-color",
                body: extractedColor
            }, "*");
        }
    }, [extractedColor, setColor]);

    return (
        <div className="z-color">
          <div
            onMouseDown={e => e.stopPropagation()}
            className="picker-container">
            <HexColorPicker color={color} onChange={onChange}/>
          </div>
        </div>);
};

export default SliderColor;
