import React, { useEffect, useCallback, useState} from 'react';
import ipfs from 'decentralized/Ipfs.js';
import {connect} from 'react-redux';
import ImageHandler from './ImageHandler.js';
import {getSkins, newSkin} from 'spores/skins.js';
import './skins.scss';

const SkinImage = ({imageHash, setImageHash, playerRef}) => { 

    let [bubble, setBubble] = useState();
    let [newImage, setNewImage] = useState();
    let [skins, setSkins] = useState([]);
    useEffect(() => {
        if (imageHash) {
            playerRef.current.contentWindow.postMessage({
                type: "texture-image",
                body: imageHash
            }, "*");
        }
    }, [imageHash]);

    useEffect(() => {
        getSkins().then(setSkins);
    }, []);

    useEffect(() => {
        if (newImage) {
            setImageHash(newImage);
        }
    }, [newImage, setImageHash]);

    const onSave = useCallback(() => {
        if (newImage) {
            setSkins([{imageHash: newImage}, ...skins]);
            setNewImage(undefined);
            newSkin(newImage).then(x => {
                setBubble(x);
                setTimeout(() => {
                    setBubble(undefined);
                }, 5000);
            });
        }
    }, [newImage, setSkins, skins, setNewImage]);

    return (
        <div className="skins-chooser">
          <div className="new-image">
            <ImageHandler
              imageHash={newImage}
              setImageHash={setNewImage}
            />
            {newImage && <div onClick={onSave} className="save-btn pushable">Save to Library</div>}
          </div>
          <div className="choices">
            {skins.map(x=>
                <img
                  onClick={() => setImageHash(x.imageHash)}
                  src={ipfs.getHashUrl(x.imageHash)}
                  className={imageHash === x.imageHash ? "skin-option selected pushable" : "pushable skin-option"}></img>)}
          </div>
          {bubble && <div className="bubble">Saved new skin to library"</div>}
        </div>
    );
};

export default SkinImage;
