import React, { useEffect, useCallback, useState} from 'react';
import ENSName from 'components/ens/ENSName.js';
import {connect} from 'react-redux';
import {fetchNFT} from 'utils/fetchNFTs.js';

const SKIP = [87, 88];
const RemixesByTokenId = ({tokenId, collectionAddress, remixes, loadRemix}) => { 
    let [data, setData] = useState(undefined);
    useEffect(() => {
        fetchNFT(collectionAddress, tokenId).then(setData);
    }, []);


    let url = `https://api.zora.co/media/ETHEREUM-MAINNET/${collectionAddress}/${tokenId}/image/thumbnail`;
    if (remixes.every(x => SKIP.includes(x.sporeId))) {
        return '';
    }
    return (
        <div className="remixes-by-token">
          <div className="token-info">
            <img src={url} classNAme="token-thumbnail"/>
            <div className="token-name">
              {!data ? "Loading..." : data.token.token.name}
            </div>
          </div>
          <div className="remix-list">
            {remixes.map(x =>
                <div
                  onClick={() => loadRemix({... x, name: data.token.token.name})}
                  className="remix-option pushable">
                  <div className="spore-id">
                    Remix #{x.sporeId}
                  </div>
                  <ENSName address={x.creator}/>
                </div>)}
          </div>
        </div>);
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(RemixesByTokenId);
