import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import RemixPlayer from './RemixPlayer.js';
import {getSporeWithHash} from 'spores/getSporeRemixes.js';
import './remix-viewer.scss';

const RemixViewer = (props) => { 
    let {recordingHash, dataHash} =props.match.params;
    let [remixData, setRemixData] = useState(undefined);
    useEffect(() => {
        getSporeWithHash(recordingHash, dataHash)
            .then(setRemixData);
    }, []);

    return (
        <div className="nft-jacker jacked remix-viewer">
          <div className={"player-container show"}>
            <RemixPlayer
              artist="Vic Mensa x Keyon Christ"
              songName="Ying Yang Twins"
              shareLink={"https://spores.vision/remix/" + recordingHash + "/" + dataHash}
              remix={remixData || {sporeHash: recordingHash, dataHash}}/>
          </div>
        </div>);
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(RemixViewer);
