import {getSporeRemixes} from 'spores/getSporeRemixes.js';

export const remixesBy = async (c, ...dims) => {
    let remixes = await getSporeRemixes(c);

    let remixesByCreator = by(remixes, ...dims);
    return remixesByCreator;
};

export const by = (results, ...keys) => {
    let map = {};

    for (let result of results) {
        let value = keys.map(x => result[x]).join('.');
        if (!map[value]) {
            map[value] = [];
        }
        map[value].push(result);
    }
    return map;
};

