import React, { useRef, useCallback, useMemo, Suspense, useEffect, useState} from 'react';
import {useVideoController} from '../video/useVideoController.js';
import {isMobile} from 'react-device-detect';
import { vertexShader } from '../video/blurred-shader/vertex.js';
import { fragmentShader } from '../video/regular-shader/fragment.js';
import { togglePlay } from '../video/control.js';
import PlayButton from 'components/buttons/PlayButton.js';
import VideoShader from '../video/VideoShader.js';
import PlaybarContainer from '../video/PlaybarContainer.js';
import VideoContentShader from '../video/VideoContentShader.js';
import * as THREE from 'three';

import SporeEmbed from 'components/player/SporeEmbed.js';
import './scene.scss';
import '../nft-jacker/nft-jacker.scss';
import '../drops/drop.scss';

import {connect} from 'react-redux';
import { Canvas, extend, useThree, useLoader, useFrame } from '@react-three/fiber';
import { OrbitControls, Stars, useAspect } from '@react-three/drei';
import { Water } from 'three-stdlib';
import './PlaneShader.js';

extend({ Water })

let PLAYER_CID = 'QmQp6vW9wgRcRDViLSQ9bwqX4q4imcTPiRqShmRCsF3KqQ';
const Scene = ({
  videoURL="https://zequencer.mypinata.cloud/ipfs/QmU6xYLumQ8TXiJXpNV3g5m5bwmeZDyigpagwdQ3b8wy7T",
  playerURL=`https://zequencer.mypinata.cloud/ipfs/${PLAYER_CID}/?tokenId=24&gallery=true`
}) => { 
    let ref = useRef();
    let playerRef = useRef();
    let {started, rewind, fastForward, isPlaying, uniforms} = useVideoController({ref, playerRef});
    let [showControls, setShowControls] = useState(false);

    useEffect(() => {
      if (!isMobile && started) {
          setShowControls(true);
      }
    }, [started, setShowControls]);

    useEffect(() => {
        if (!isPlaying && started) {
            setShowControls(true);
        } else if (isPlaying && isMobile) {
            setTimeout(() => {
                setShowControls(false);
            }, 1000);
        }
    }, [isPlaying, setShowControls, started]);

    const togglePlayControls = useCallback(() => {
        if (!isMobile) {
            return;
        }
        setShowControls(!showControls);
    }, [showControls, setShowControls]);

    let className = "drop nft-jacker jacked video-jacker";
    if (isMobile && !started) {
        className += " dark-setting";
    }

    return (
      <>
      <div className={className}>
              <SporeEmbed
                playerRef={playerRef}
                videoRef={ref}
                innerControls={isMobile}
                songTitle="VERIFIED FEAT. CHIIEF DIIN" 
                artist="HALEEK MAUL"
                hideControls={true}
                url={playerURL}>
                {isMobile && started && <VideoShader 
                               uniforms={uniforms}
                  fragmentShader={fragmentShader}
                  vertexShader={vertexShader}
                  videoRef={ref}
                  width={window.innerWidth}
                  height={isMobile ? window.innerHeight*0.7 : window.innerHeight}
                             />}
              </SporeEmbed>
              <div className="z-50 h-1/3 md:z-10 fixed md:h-full w-full flex items-center justify-center">
                {isMobile && <VideoContentShader
                  started={started}
                   videoRef={ref} uniforms={uniforms}/>}
                <video
                  preload="auto"
                  preLoad="auto"
                  onClick={togglePlayControls}
                  className="absolute md:h-2/3 md:w-auto md:top-1/2 md:left-1/2 md:transform md:-translate-x-1/2 md:-translate-y-1/2 w-full"
                  style={{opacity: 0}}
                  crossorigin=""
                  ref={ref}
                  muted
                  playsInline
                  src={videoURL}>
                </video>
                <PlaybarContainer videoRef={ref} playerRef={playerRef} started={started}/>
              </div>
              {!isMobile && <Canvas
                  style={{position: "absolute"}}
                  shadows
                  dpr={[1, 2]}
                  camera={{
                      fov: 20,
                      zoom: 0.2,
                      near: 1,
                      far: 1000,
                      position: [0, 0, 20],
                      up: [0, 1, 0]
                  }}>
                    <color attach="background" args={["#000000"]}/>
                    <OrbitControls
                      enablePan={false}
                      enableRotate={false}
                      enableZoom={false}
                      minDistance={10}/>
                    <directionalLight position={[5,5,0]}/>
                    <ambientLight/>
                    <Stars radius={150} depth={50} count={2000} factor={4} saturation={0} fade speed={1} />
                    <Suspense fallback={null}>
                      <Ocean/>
                      <VideoObject/>
                    </Suspense>
                  </Canvas>}
              {showControls &&
               <div className="play-controls z-50 absolute top-8 md:bottom-14 left-0 right-0 mx-auto w-4 md:w-28">
                 <img onClick={rewind} className="rew pushable" src="/rewind-icon.svg"/>
                 <PlayButton isPlaying={isPlaying} play={() => togglePlay(playerRef)}/>
                 <img onClick={fastForward} className="fwd pushable" src="/fast-forward-icon.svg"/>
               </div>}
           </div>
            </>
           );

  function VideoObject() {
    const size = useAspect(1920, 1080, 3);
    return (
      <>
      <mesh scale={size}>
        <planeGeometry/>
        <planeShader
          attach="material"
          video={new THREE.VideoTexture(ref.current)}
          width={window.innerWidth/2}
          height={window.innerHeight/2}
          videoUniforms={uniforms}
        />
      </mesh>
      </>
    )
  }
};

function Ocean() {
  const ref = useRef();
  const gl = useThree((state) => state.gl);
  const waterNormals = useLoader(
    THREE.TextureLoader, "https://raw.githubusercontent.com/mrdoob/three.js/master/examples/textures/waternormals.jpg"
  );

  waterNormals.wrapS = waterNormals.wrapT = THREE.RepeatWrapping;
  const geom = useMemo(() => new THREE.PlaneGeometry(10000, 10000), []);
  const config = useMemo(
    () => ({
      textureWidth: 512,
      textureHeight: 512,
      waterNormals,
      sunDirection: new THREE.Vector3(),
      sunColor: 0xeb8934,
      waterColor: 0x0064b5,
      distortionScale: 40,
      fog: false,
      format: gl.encoding,
    }),
    [waterNormals]
  );
  useFrame(
    (state, delta) => (ref.current.material.uniforms.time.value += delta)
  );
  return (
    <water
      ref={ref}
      args={[geom, config]}
      rotation-x={-Math.PI / 2}
      position={[0, -12, 0]}
    />
  );
}

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Scene);
