import BackendQuery from 'decentralized/BackendQuery.js';
import {ethers} from 'ethers';
import {NewSkin} from 'decentralized/EthereumEvents.js';
import {web3} from 'decentralized/web3.js';

export const getSkins = async () => {
    let query = new BackendQuery('0x723B26f588C57b393C8f6793796A82F479010C85')
        .from(NewSkin);

    let unfiltered = await query.executeQuery();
    return unfiltered;
}

export const newSkin = (imageHash) => {
    return new Promise(resolve => {
        let baseURL = "https://zequencer.io";
        if (window.location.hostname.includes("localhost")) {
            baseURL = "";
        }
        
        let body = {
            imageHash
        };
        
        fetch(baseURL + '/newSkin/' , {
            method: 'POST',
            body: JSON.stringify(body),
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
            }
        })
            .then(resp => resp.json())
            .then(resp => {
                resolve(resp);
            });
    });
};
 
